.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container-fluid,.row,.col-2,.col-6,.col-4{
  height: 100%;
}
.navbar {
  height: 60px;
  background-color: slategrey;
}
#navbar {
  padding-left: 15px;
}
.row.no-gutter {
  margin-left: 0;
  margin-right: 0;
}
.row.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}
#code{
  overflow-y: scroll;
  height: 535px;
}
#instruction {
  height: 50px;
  overflow-y: scroll;
}
#execution{
  overflow-y: scroll;
  height: 160px;
}
#slider {
  width: 90%;
}